<template>
  <div class="login">
      <div class="left">
        <option-img></option-img>
      </div>
      <div class="right clear">
        <div>
          <p><span>登录</span></p>
          <label><img :src="accountimg" alt=""><input v-model="account" type="text" name="account" id="account" placeholder="OA账号" @focus="changeTip"  @keyup.enter="signUp"></label>
          <label><img :src="cipherimg" alt=""><input v-model="cipher" :type="pwdType" name="cipher" id="cipher" placeholder="密码" @focus="changeTip"  @keyup.enter="signUp"><img :src="seen?openeye:nopeneye" class="eye" alt="" @click="changeType"></label>
          <label><img :src="verificationCodeimg" alt=""><input autocomplete="off" v-model="code" type="tel" name="verificationCode" id="verificationCode" placeholder="验证码" @focus="changeTip"  @keyup.enter="signUp">
          <!--验证码组件-->
          <div class="yzm" @click="refreshCode">
            <s-identify :identifyCode="identifyCode"></s-identify>
            <img :src="changeCodeimg" alt="" class="change-img">
          </div>
          </label>
          <button class="login-btn" @click="signUp">登录</button>
          <button class="forget-password" @click="findPassword">忘记密码？</button>
        </div> 
        <div class="tips" v-if="errorSeen">
           <el-alert
            :title="errorContent"
            type="error"
            show-icon
            :closable=false>
          </el-alert>
        </div>
      </div>
  </div>
</template>

<script>
import SIdentify from '../components/sidentify/identify.vue'
import optionImg from '../components/optionImg/optionImg.vue'
import { constants } from 'fs'
export default {
  name: 'login',
  components:{SIdentify,optionImg},
  data () {
    return {
      seen:'',
      errorSeen: false,
      pwdType:'password', // 密码类型,
      identifyCodes: "1234567890abcdefghijklmnopqrstuvwsyzABCDEFGHIJKLMNOPQRSTUVWSYZ",
      identifyCodesN: "1234567890",
      identifyCode: "",
      code:"",//text框输入的验证码
      errorContent:'',
      account:'',//OA账号
      cipher:'',//密码
      loading: false,
      openeye: require('@/assets/eye.png'), //图片地址
      nopeneye: require('@/assets/noeye.png'), //图片地址
      accountimg:require('@/assets/account.png'), //图片地址
      cipherimg:require('@/assets/cipher.png'), //图片地址
      verificationCodeimg:require('@/assets/verificationCode.png'),
      changeCodeimg:require('@/assets/change.png')//更换验证码
    }
  },
  created(){
    this.refreshCode();
  },
  mounted(){
    this.identifyCode = "";
    this.makeCode(this.identifyCodes, 4);
  },
  methods : {
      //登录按钮
      signUp: function(){
        const _that = this;
        console.log(this.code, this.identifyCode);
        if(this.account == '' || this.cipher == ''){//账号或者密码为空
            this.errorContent = '账号或者密码不能不空'
            this.errorSeen = 'true';
            this.refreshCode();
        }else{//账号或者密码不为空
            if(this.code != '' && this.identifyCode == this.code){//验证码不为空且相同
                this.$axios.post('/index/login', {
                userName:this.account,
                password:this.cipher
              }).then(function (response) {
                if(response.data.code != 200){
                  if(response.data.code == 202){//密码过期
                    _that.errorContent = response.data.msg;
                    _that.errorSeen = 'true';
                    setTimeout(function(){
                      _that.$router.push({name:'changePassword',params:{"accountOA":_that.account}});
                    },2000)
                  
                  }else{//其他错误信息
                    _that.errorContent = response.data.msg;
                    _that.errorSeen = 'true';
                    _that.refreshCode();
                  console.log(response,response.data.code);
                  }
                }else{
                 
                  _that.errorSeen = false;
                  _that.$message({
                      message: '恭喜你，'+response.data.msg,
                      type: 'success'
                    });
                  window.localStorage.setItem('_pc_token', JSON.stringify({_pc_userName:_that.account }));
                  _that.$router.push({name:'home',params:{"accountOA":_that.account}});
                
                }
              }).catch(function (error) {
                console.log(error);
              });
            }else if(this.code == ''){
                this.errorContent = '验证码不能为空，请填写验证码'
                this.errorSeen = 'true';
                this.refreshCode();
            }else if(this.identifyCode != this.code){
              this.errorContent = '验证码输入有误，请填写正确的验证码'
              this.errorSeen = 'true';
              this.refreshCode();
            }
        }
      },


      //忘记密码
      findPassword:function(){
        this.$router.push('../retrieve')
      },
      //密码是否可见
      changeType() {
        this.pwdType = this.pwdType === 'password' ? 'text' : 'password';
        this.seen = !this.seen;//小眼睛的变化
       },
       //验证码
      
     randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      refreshCode() {
        this.identifyCode = "";
        this.makeCode(this.identifyCodes, 4);
      },
      makeCode(o, l) {
        for (let i = 0; i < l; i++) {
          if(i == 0){
            this.identifyCode += this.identifyCodesN[this.randomNum(0, this.identifyCodesN.length)];
          }else{
            this.identifyCode += this.identifyCodes[this.randomNum(0, this.identifyCodes.length)];
          }
        }
        console.log(this.identifyCode);
      },
      //监听input是否聚焦
      changeTip: function(){

        if (document.getElementById('account') == document.activeElement ||
            document.getElementById('cipher') == document.activeElement ||
            document.getElementById('verificationCode') == document.activeElement) {
              this.errorSeen = false ;
              console.log('获取焦点');
         } 
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
$height-content : 300px;

.login{
  background: #fff;
  width: 1000px;
  min-height: $height-content;
  border-radius: 5px;
  margin: auto;
  margin-top: 5%;
  .right{
    width: 600px;
    height: $height-content;
    float: right;
    position: relative;
    p{
     text-align: center;
    }
    label{
      display: block;
      border: 1px solid #7f7f7f;
      border-radius: 5px;
      width: 300px;
      height: 40px;
      margin: auto;
      margin-top: 5px;
      position: relative;
      img:first-child{
        width: 25px;
        padding: 0 10px;
        position: relative;
        left: 0;
        top: 50%;
        transform: translate(0,-50%);
      }
      img:last-child{
        position: absolute;
        right: 0;
        top: 50%;
        transform: translate(0,-50%);
        width: 25px;
        padding: 0 10px;
      }
      img.change-img{
        padding: 0;
        width: 20px;
        right: 4px;
      }
      input{
        font-size: 14px;
        height: 25px;
        width: 230px;
        border: none;
        outline: none;
      }
      input[name="verificationCode"]{
         width: 130px;
      }
      .yzm{
        display: inline-block;
        width: 120px;
        vertical-align: middle;
        border-radius: 5px;
        box-sizing: border-box;
        cursor: pointer; 
      }
    }
    button{
      margin: auto;
      display: block;
      width: 300px;
      height: 40px;
      cursor: pointer;
      border-radius: 5px;
      border: none;
      font-size: 16px;
    }
    button.login-btn{
      background: #FFBE5A;
      margin-top: 40px;
      color: white;
    }
    button.forget-password{
        background: inherit;
        width: auto;
        height: 30px;
        padding: 0 10px;
        font-size: 13px;
        float: right;
        margin-right: 140px;
    }

    button.forget-password:hover{
      background: inherit !important;
      color: #ffa331;
    }
    .tips{
      position: absolute;
      min-width: 300px;
      border-radius: 5px;
      left: 50%;
      bottom: 55px;
      transform: translate(-50%, -50%);
      p{
        color: white;
        font-size: 13px;
      }
    }
  }
}
.clear {zoom:1;}    /*==for IE6/7 Maxthon2==*/
.clear::after {
   clear:both;
   content:'.';
   display:block;
   width: 0;
   height: 0;
   visibility:hidden;
}
</style>



